import { Element } from "react-scroll";
import img from '../images/time-table.jpg'
import header from '../images/time-table-header.jpg'
import logo from '../images/main-logo.png'
import { useInView } from "react-intersection-observer";
import styled from "styled-components";
import { Card, Col, Container, Row  } from "react-bootstrap";
import { Icon } from '@iconify/react';
import { useIsMobile } from "../hooks/useIsMobile";
const Section = styled.section`
  min-height: 100vh;
  overflow: hidden;
  padding-top: var(--nav-height);
  opacity: 0;
  transition: 1.5s all ease-in-out;
  white-space: pre-line;

  &.in {
    opacity: 1;
  }


  .card {
    border: none;
    border-radius: 10px;
    height: 100%;
  }

  .c-details span {
      font-weight: 300;
      font-size: 0.8rem;
  }

.icon {
    width: 50px;
    height: 50px;
    background-color: #eee;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem
}

.badge span {
    height: 25px;
    padding: 3px 5px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.infra {
  background-color: #ebf5ff;
  color: #3d6efd;
}

.overview {
  background-color: #edf7ed;
  color: #50c878;
}

.exp {
  background-color: #ffe6f5;
  color: #ff69b4;
}

.description {
    font-size: 0.9rem;
    font-weight: 600
    color: #a5aec0
}

.shake {
  animation: shake 5s ease infinite;
}
@keyframes shake {
	0%, 100% {transform: translateX(0);}
	10%, 30%, 50%, 70%, 90% {transform: translateX(-5px);}
	20%, 40%, 60%, 80% {transform: translateX(5px);}
}
`;

export default function Programs() {
  const {ref, inView} = useInView({root:null, rootMargin: "0px", threshold: 0});
  const isMobile = useIsMobile();

  const infra = [
    {icon: 'mdi:sql-query', category: 'Infra', name: '', time: '[] 15:10 ~ 15:40', title: 'jOOQ, SQL   ', link: '', description: ' SQL       jOOQ   .'},
    {icon: 'mdi:sql-query', category: 'Infra', name: '', time: '[] 15:50 ~ 16:20', title: 'JPA Patterns', link: '', description: '  JPA       .'},
    {icon: 'fluent:arrow-move-24-filled', category: 'Infra', name: '', time: '[] 14:30 ~ 15:00', title: ' :  ', subTitle: '(    )', description: '          .'},
  ]
  const overview = [
    {icon: 'mdi:language-kotlin', category: 'Overview', name: '', time: '[] 16:50 ~ 17:20', title: 'Kotlin Injection', subTitle: '(      )', description: '             .'},
    {icon: 'simple-icons:openjdk', category: 'Overview', name: '', time: '[] 17:30 ~ 18:00', title: 'JDK Structured Concurrency', link: '', description: 'JDK21  Preview  Structured Concurrency  .'},
    {icon: 'mdi:graphql', category: 'Overview', name: '', time: '[] 15:10 ~ 15:40', title: '  API  ,  ', link: '', description: '  API       API     .'},
    {icon: 'devicon-plain:grpc', category: 'Overview', name: '', time: '[] 15:50 ~ 16:20', title: 'gRPC ', link: '', description: 'REST        .'},
  ]
  const exp = [
    {icon: 'mingcute:performance-line', category: 'Experience', name: '', time: '[] 17:30 ~ 18:00', title: '2     ', link: '', description: '        .'},
    {icon: 'wpf:past', category: 'Experience', name: '', time: '[] 16:50 ~ 17:20', title: '  ', subTitle: '(   )', description: '               .'},
    {icon: 'ri:team-line', category: 'Experience', name: '', time: '[] 14:30 ~ 15:00', title: '    ', link: '', description: '       .'},
  ];
  const alls = {
    infra,
    overview,
    exp
  }

  return (
    <Element name={"Programs"} id="programs">
      <Section className={`${inView ? 'in' : ''}`} ref={ref}>
      <Container className="my-3">
          <div className="mb-5" role="button" onClick={() => window.open(img, '_blank', 'noopener')}>
            <img src={header} className="w-100"/>           
          </div>
        
          {Object.entries(alls).map(([key, value]) => (
            <Row>
            {value.map((speaker) => (
              <Col lg={3} className="mb-3">
              <Card className="p-3">
                <div className="d-flex justify-content-between">
                  <div className="d-flex flex-row align-items-center">
                    <div className="icon"><Icon icon={speaker.icon} /></div>
                    <div className="ms-2 c-details">
                      <h6 className="mb-0">{speaker.name}</h6>
                      <span>{speaker.time}</span>
                    </div>
                  </div>
                  <div className="badge"><span className={`${key}`}>{speaker.category}</span></div>
                </div>
                <div className="mt-5 h-100 d-flex flex-column">
                  <h3 className="flex-grow-1">{speaker.title}</h3>
                    <div className={`mt-3 ${isMobile ? 'd-none' : ''}`}>
                      <span className="description">{speaker.description}</span>
                    </div>
                </div>
              </Card>
            </Col>
            
            ))}
          {(value.length === 3) && (
            <Col lg={3} className="mb-3 d-none d-lg-block">
            <Card className="p-3 d-flex flex-column justify-content-center">
              <img src={logo} width='50%' className="mx-auto"/>
            </Card>
          </Col>
          )}
            </Row>
          ))}
      </Container>
      </Section>
    </Element>
  );
}
